.homepage {
  display: block;

  .cover-area {
    width: 100%;
    height: 650px;
    margin: 0;

    .cover-image {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 0;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: left top;
    }
  }

  .slider-container {
    width: 90%;
    display: block;
    margin: 0 auto;

  img {
    margin: 0 auto;
  }

  }

}

.slick-prev::before, .slick-next::before {
  color: grey;
  opacity: 0.7;
}